<template lang="pug">
    .test__item-content
        .test__item-row
            .tests__number {{idx+1}}
            .tests__code  {{test.code}}
            .tests__subject {{test.group.subject.name}}
            .tests__class  {{test.group.groupClass}}
            .tests__class  {{test.duration}}
            .buttons
                button(@click.prevent='testExpandEvent()' v-if='!testVisible' :id='test.id').test__view-expand
                button(@click.prevent='testHideEvent()' v-if='testVisible').test__view-hide
        available-test-item-container(
            :test='test'
            v-if='testVisible'
        )
        .button__remove
            button(@click='removeTest()' v-if="testVisible").remove__programme Удалить тест
        
</template>
<script>
import { mapActions } from "vuex";
export default {
    components: {
        availableTestItemContainer: () => import("./available-test-item-container")
    },
    data() {
        return {
            testVisible: false
        }
    },
    props: {
        test: Object,
        idx: Number
    },
    methods: {
        ...mapActions("tests", ["deleteTest"]),
        removeTest(){
            this.deleteTest(this.test.id)
            this.testHideEvent()
        },
        testExpandEvent(){
            this.testVisible=!this.testVisible
            let buttons = document.getElementsByClassName('test__view-expand')
            buttons.forEach(elem => {
                elem.disabled=true
            });
            document.getElementById(this.test.id).removeAttribute('disabled')
        },
        testHideEvent() {
            this.testVisible=!this.testVisible
            let buttons = document.getElementsByClassName('test__view-expand')
            buttons.forEach(elem => {
                elem.disabled=false
            });
        }
    }
}
</script>
