    width: 5%;
}
.tests__subject, .tests__class,.buttons {
    width: 22%;
}
.tests__code {
    width: 30%;
}
.tests__class, .buttons {
    text-align: right;
}
.test__view-expand {
    background-color:#464549;
    width: 25px;
    height: 25px;
    border-radius: 5px;
    position: relative;
    &:hover {
        opacity: .8;
        transition: .5s;
        cursor: pointer;
    }
    &:before {
        content: '';
        display: block;
        width: 3px;
        height: 11px;
        background: #FFFAF0;
        position: absolute;
        left: 50%;
        top: 50%;
        transform: translate(-50%, -50%);
    }
    &:after {
        content: '';
        display: block;
        width: 3px;
        height: 11px;
        background: #FFFAF0;
        position: absolute;
        left: 50%;
        top: 50%;
        transform: translate(-50%, -50%) rotate(90deg);
    }
}
.test__view-hide {
    background-color:#464549;
    width: 25px;
    height: 25px;
    border-radius: 5px;
    &:hover {
        opacity: .8;
        transition: .5s;
        cursor: pointer;
    }
    &:after {
        content: '';
        display: block;
        width: 10px;
        border-radius: 2px;
        height: 3px;
        background: #FFFAF0;
        background-size: cover;
    }
}
</style>